.card{
    position: relative;
    background-color: white;
    width: 50vw;
    padding: 20px;
    max-width: 300px;
    height: 50vh;
    border-radius: 20px;
    background-size: cover;
    background-position: center;
    
}

.tinderCards__cardContainer{
    display: flex;
    justify-content: center;
    margin-top:10vh;
    
}
.swipe{
    position: absolute;
}


.cardContent{
    width: 100%;
    height: 100%;
}

.card h3{
    position: absolute;
    bottom: 0;
    margin: 10px;
    color: #fff;
}
